	}
	
	.item p {
		font-size: 16px;
		color: red;
		padding: 10px 0 15px 0;
	}
	
	.item h6 {
		font-size: 12px;
		line-height: 22px;
		color: #555;
		padding-bottom: 15px;
	}
	
	.item img {
		width: 100%;
		margin-bottom: 10px;
	}
	
	.item {
		width: 100%;
		height: auto;
	}
	
	.cont {
		float: left;
		width: 25%;
		padding: 0 10px;
		box-sizing: border-box;
		margin-bottom: 20px;
		margin-top: 10px;
		padding-top: 20px;
	}
	
	.cent {
		width: 100%;
		overflow: hidden;
		padding-bottom: 50px;
	}
	
	.list {
		overflow: hidden;
		height: 86px;
/*		background-color: rgba(244, 244, 244, 0.5);*/
	}
	
	.list ul {
		max-width: 520px;
		margin: 0 auto;
		max-height: 86px;
		line-height: 66px;
		font-size: 15px;
	}
	
	.list li {
		float: left;
		color: #878787;
		padding: 0 10px 0 10px;
		text-align: center;
		margin: 0 20px;
	}
</style>


