import Vue from 'vue'
import Router from 'vue-router'
import no1 from './router/no1.vue'
import no2 from './router/no2.vue'
import no3 from './router/no3.vue'
import no4 from './router/no4.vue'
import no5 from './router/no5.vue'
import cat_info from './router/cat_info.vue'
import shop from './router/shop.vue'

Vue.use(Router)

export default new Router({
	routes:[
		{path:'/',component:no1},
		{path:'/no2',component:no2},
		{path:'/no3',component:no3},
		{path:'/no4',component:no4},
		{path:'/no5',component:no5},
		{path:'/cat_info',component:cat_info},
		{path:'/shop',component:shop}
	]
})



